/**
 *   rel  
 * @param {string} location
 */
export const getRel = location => {
    let rel = ['nofollow'];

    if (/^https?:/.test(location)) {
        rel.push('noopener');
    }

    return rel.join(' ');
};

/**
 *    -
 * @param {string} name
 * @param {string} value
 */
export const getMetaTag = (name, value) => document.querySelector(`meta[${name}="${value}"]`);

/**
 *    - og-
 * @param {string} name
 */
export const getOgTag = name => getMetaTag('property', `og:${name}`);
